import {
  LitElement,
  html,
  customElement,
  property,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import {
  ManifestPropertyEditorUi,
  UmbPropertyEditorUiElement,
} from "@umbraco-cms/backoffice/property-editor";

type PropertyEditorValueType = {
  text?: string;
  size?: string;
};

const SizeOptions = [
  { value: "H1", name: "H1" },
  { value: "H2", name: "H2" },
  { value: "H3", name: "H3" },
  { value: "H4", name: "H4" },
  { value: "H5", name: "H5" },
  { value: "H6", name: "H6" },
];

@customElement("textbox-heading-property-editor")
export class textboxHeadingPropertyEditorElement
  extends UmbElementMixin(LitElement)
  implements UmbPropertyEditorUiElement
{
  @property({ type: Object, attribute: false })
  manifest?: ManifestPropertyEditorUi;

  @property({ type: Object, attribute: false })
  value?: PropertyEditorValueType;

  render() {
    return html`
      <div style="display: flex; width: 100%;">
        <div style="flex: 0 0 80%;">
          <uui-input
            id="text"
            value=${this.value?.text ?? ""}
            style="width: 100%;">
          </uui-input>
        </div>
        <div style="flex: 0 0 20%; margin-left: 2px;">
          <uui-select
            label="Select size"
            placeholder="Select size..."
            value=${this.value?.size ?? ""}
            style="width: 100%;"
            .options=${
              SizeOptions.map((e) => ({
                ...e,
                selected: e.value == this.value?.size,
              }))
            }>
           </uui-select>
        </div>
      </div>
    `;
  }
}

export default textboxHeadingPropertyEditorElement;

declare global {
  interface HTMLElementTagNameMap {
    "textbox-heading-property-editor": textboxHeadingPropertyEditorElement;
  }
}